import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Home from './pages/home'
import Counter from './pages/counter'

function App() {
  return (
    <Router basename={`/${process.env.PUBLIC_URL}`}>
      <Switch>
        <Route path='/ProjectOne/counter'>
          <Counter />
        </Route>
        <Route path='' exact>
          <Home />
        </Route>
        <Route path='/ProjectOne/bork' component={Counter} />
      </Switch>
    </Router>
  )
}

export default App;
